/* ==========================================================================
   #Modal
   ========================================================================== */

  /**
   * Component to show an accessible pop-up window (modal).
   *
   */

   .simple-animated-modal-overlay {
     animation:fadewhite ease .05s 1 normal;
     background:colour(rpb-black);
     border-color: transparent;
     bottom:0;
     cursor: pointer;
     left:0;
     opacity: 0.8;
     position: fixed;
     right:0;
     top:0;
     z-index: z(overlay);

     &[data-background-click=disabled] {
      cursor:auto;
     }

   }

   @keyframes fadewhite {
    0% {
     opacity:0
    }
    100% {
     opacity:.8
    }
   }

   .simple-animated-modal {
    -webkit-animation: apparition ease .25s 1 normal;
    animation: apparition ease .25s 1 normal;
    background-color: colour(rpb-white);
    border-radius: 8px;
    border: none;
    box-shadow: 0 3px 6px 0 rgba(0,0,0,.5);
    height: auto;
    left: 1%;
    max-height: 90vh;
    overflow: auto;
    padding: $null 1% $xs;
    position: fixed;
    right: 24%;
    top: 5%;
    width: 98%;
    z-index: z(modal);

    @include mq($from: tablet) {
      left: 5%;
      width: 90%;
    }    
    
    @include mq($from: desktop) {
      left: 24%;
      top: 6vh;
      width: 50%;
    } 



   }

   .simple-animated-modal-close {
    background: 0 0;
    border: 0;
    cursor: pointer;
    height: 30px;
    outline: 0;
    overflow: hidden;
    position: absolute;
    right: 2%;
    top: 15px;
    width: 30px;

    &:before,
    &:after {
     background:colour(rpb-blue);
     border-radius:10px;
     content:"";
     height:5px;
     margin-top:-1px;
     position:absolute;
     right:0;
     top:50%;
     width:100%;
    }

    &:before {
      transform:rotate(45deg)
     }
    &:after {
      transform:rotate(-45deg)
    }

    &:focus {
      background-color: colour(rpb-focus);
      box-shadow: 0 -2px colour(rpb-focus), 0 4px colour(rpb-link-active);
      color: colour(rpb-link-active);
      text-decoration: none;

      &:before,
      &:after {
        background-color: colour(rpb-slate);
      }
    }


   }

   //keyframes
   @-webkit-keyframes pop {
    0% {
     opacity:0;
     -webkit-transform:scale(0);
     transform:scale(0)
    }
    100% {
     opacity:1;
     -webkit-transform:scale(1);
     transform:scale(1);
     max-height:100%;
     width:30%;
     left:35%
    }
   }
   @keyframes pop {
    0% {
     opacity:0;
     -webkit-transform:scale(0);
     transform:scale(0)
    }
    100% {
     opacity:1;
     -webkit-transform:scale(1);
     transform:scale(1);
     max-height:100%;
     width:70%;
     left:15%
    }
   }


  .rpb-modal {
    position: relative;
    padding-bottom: 56.25%;
    height: 0;
    overflow: hidden;
    max-width: 100%;
    border: 0;
    outline: 0;
    margin-bottom: 10px;
  }

.rpb-model__image {
  display: block;
  margin:0 auto;
  max-width:95%;
  position: relative;

}