.back-to-top {
  background-color: #FFD72F;
  border: none;
  border-radius: 50%;
  bottom: -3.5rem;
  box-shadow: 0 2px 4px 0 rgba(0,0,0,.85);
  color: colour(rpb-slate);
  cursor: pointer;
  height: $null;
  line-height: 1.5;
  margin: 0;
  opacity: 0;
  padding: $md;
  position: fixed;
  right: 1rem;
  transition: .25s all ease-in-out;
  width: $null;
  z-index: z(modal);

  &:before {
    bottom: 0;
    color: colour(rpb-slate);
    content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%232A2A2A' d='M12 5.5c.4 0 .9.2 1.2.5l10.2 10.2c.7.7.7 1.7 0 2.4s-1.7.7-2.4 0l-9-9.1-9.1 9.1c-.7.7-1.7.7-2.4 0s-.7-1.7 0-2.4L10.8 6c.4-.3.8-.5 1.2-.5z'/%3E%3C/svg%3E");
    height: $md;
    left: 0;
    margin: auto;
    position: absolute;
    right: 0;
    top: 0;
    width: $md;
  }

  &.active {
    bottom: 2.5rem;
    opacity: 1;

    &:focus:not(:hover) {
        background-color: rgba(255,233,0,1);
        border-bottom: 2px solid transparent;
        box-shadow: 0 0px colour(rpb-focus),0 3px colour(rpb-link-active);
        color: colour(rpb-link-active);
        outline: 3px solid transparent;
        text-decoration: none;
    }
  }

  &:hover:not(:focus) {
    background-color: #FFAF00;
  }

}








