/* ==========================================================================
   #Spacings
   ========================================================================== */

/*
 * Specific spacing values for elements. Spacing base values are in 
 * ./core/settings.scss
 *
 * Reference: https://css-tricks.com/magic-numbers-in-css/
 *
 *   .rpb-m {}      =  .rpb-margin {}
 *   .rpb-mt {}     =  .rpb-margin-top {}
 *   .rpb-pl-lg {}  =  .rpb-padding-left-large {}
 *   .rpb-mr-sm {}  =  .rpb-margin-right-small {}
 *   .rpb-p {}      =  .rpb-padding {}
 *   .rpb-pr-nil {} =  .rpb-padding-right-none {}
 *   .rpb-ph {}     =  .rpb-padding-horizontal {}
 *   .rpb-pv-sm {}  =  .rpb-padding-vertical-small {}
 *
 *   e.g.
 *
 *   <div class="rpb-pt-lg"></div>
 *
 *   Will render as:
 *
 *   .rpb-pt-lg{
 *       padding-top: 3.0em;
 *    }
 *  
 *
 *   When adding elements to classes:
 *
 *  .example {
 *     padding: $xs $lg 0 $xs;
 *   }
 *    
 *   Will render as:
 *
 *   .example {
 *     padding: .375em 3.0em 0 .750em;
 *    }
 *   
 */

 
 //Generate a suite of shorthand classes:
 $rpb-spacing-directions: (
  null: null,
  "t": "-top",
  "r": "-right",
  "b": "-bottom",
  "l": "-left",
  "h": "-left" "-right",
  "v": "-top" "-bottom",
) !default;

$rpb-spacing-properties: (
  "p": "padding",
  "m": "margin",
) !default;

$rpb-spacing-sizes: (
  "-xs"    :   $xs,
  "-sm"    :   $sm,
  "-sp"    :   $sp,
  "-md"    :   $md,
  "-lg"    :   $lg,
  "-xl"    :   $xl,  
  "-xxl"   :   $xxl,
  "-flush" :   0
) !default;

@each $property-namespace, $property in $rpb-spacing-properties {

  @each $direction-namespace, $direction-rules in $rpb-spacing-directions {

    @each $size-namespace, $size in $rpb-spacing-sizes {

      .rpb-#{$property-namespace}#{$direction-namespace}#{$size-namespace} {

        @each $direction in $direction-rules {
          #{$property}#{$direction}: $size;
        }

      }

    }

  }

}

