/* ========================================================================
   #Masthead
   ======================================================================== */

/**
 * Background-image @mixin is imported, file naming format ends with -sm, -sm, 
 * -lg include image name and file type, i.e.
 *
 * File name: image-sm.jpg, image-sm.jpg, image-lg.jpg
 *
 * .example {
 *   @include background-image("image-name", jpg);
 * }
 *
 */



//Import masthead mixin 
@import "tools/masthead";



//Rpb banner
.rpb-masthead {
  background: #FFD72F;
  background: linear-gradient(90deg,#0F296A,#FFD72F 82%);
  display: block;
  margin-top: $md;
  overflow: hidden;
}

.rpb-masthead__image {
  @include background-image("robbie", png);
  background-repeat: no-repeat;
  background-position: 5%;
  background-size: contain;
  height: 175px;
  margin-top: $lg;
  transition: opacity .25s ease-in-out;

  @include mq($from: tablet ) {
    background-position: 50%;
    height: 350px;
  }  

  
  @include mq($from: desktop ) {
    background-position: 45%;
    height: 450px;
  } 

}

// Banner content
.rpb-masthead__content {
  display: flex;
  position: relative;

  &.rpb-grid--one-half {
    width: auto;
  }

}

.rpb-masthead--wrapper {
  display: inline;
 }

.rpb-masthead__description {
  
  left: auto;
  line-height: 1.25;
  margin: 0;
  max-width: 50%;
  padding: $sm $sm $null;
  color: colour(rpb-slate);
  right: 0;
  width: auto;
  position: absolute;
  top: 30px;
  z-index: z(footer); 

  @include mq($from: tablet ) {
    max-width: 55%;
    right: $sm;
    top: 30%;
    transform: translateY(-30%);
    width: 100%;
  }  

  @include mq($from: desktop ) { 
    max-width: 45%;
  }

  .rpb-h1 {
    @include font-size(28px);

    @include mq($from: tablet ) {
      @include font-size(48px);
    }  
  }



  //Toggle
  &.active > .rpb-masthead-toggle__button:before {
    left:9px;
    transform: rotate(-45deg);
    top: 11px;
  }

  &.active > .rpb-masthead--wrapper {
    display: block;
  }

}

.rpb-masthead-toggle__button {
  background: none;
  border: none;
  border-radius: 0;
  cursor: pointer;
  display: block;
  height: $lg;
  outline: none;
  padding:$xs;
  position: absolute;
  right: 10px;
  top: 13px;
  width: $lg;

  &:before {
    border-top: 3px solid colour(rpb-white);
    border-right: 3px solid colour(rpb-white);
    content: "";
    height: $sm;
    left: 5px;
    margin-top: -.025rem;
    pointer-events: none;
    position: absolute;
    top: 3px;
    transform: rotate(135deg) translateY(-50%);
    width: $sm;
  }

  &:after {
    background-color: transparent;
    content: " ";
    display: block;
    height: 1px;
    left: 0;
    right: 0;
    position: absolute;
    bottom: 0;
  }

  &:focus:not(:hover) {
    background-color: colour(rpb-focus);
    box-shadow: 0 -2px colour(rpb-focus), 0 4px colour(rpb-link-active);
    color: colour(rpb-link-active);
    outline: 3px solid transparent;
    text-decoration: none;
  }

  @include mq($from: phablet) {
    display: none;
   } 

}







