/* ==========================================================================
   #Flexbox
   ========================================================================== */

/*
 * Flexbox grid system. Support is for IE11+ 
 *
 * Cells are full-width and will stack as rows by default, media-queries are 
 * based off ./tools/media-queries.scss and ./objects/widths.
 *
 * <div class="rpb-grid">
 *   <div class="rpb-grid__cell">
 *   </div>
 *   <div class="rpb-grid__cell">
 *   </div>
 * </div>
 *
 * Cells will in most cases be accompanied by utility classes that divide
 * the grid into fractions:
 *
 * <div class="rpb-grid">
 *   <div class="rpb-grid__cell rpb-grid-one-third">
 *   </div>
 *   <div class="rpb-grid__cell rpb-grid-two-thirds">
 *   </div>
 * </div>
 *
 * Cells can be spaced equally using rpb-grid--auto without the need for 
 * utility classes:
 *
 *  <div class="rpb-grid rpb-grid--auto">
 *   <div class="rpb-grid__cell">
 *     50%
 *   </div>
 *  <div class="rpb-grid__cell">
 *    50%
 *  </div>
 * </div>
 *
 * Order of cells can be changed, use with caution when ordering content
 * in a meaningful sequence:
 *
 *  <div class="rpb-grid rpb-grid--auto">
 *   <div class="rpb-grid__cell rpb-order-2">
 *     50%
 *   </div>
 *  <div class="rpb-grid__cell rpb-order-1">
 *    50%
 *  </div>
 * </div>
 *
 */
 @import "objects/widths";

// Specify a map of possible gutter widths. By default use the global
// spacing units from core.scss and their conventional class
// name suffixes (--small, --medium, --large etc).

$rpb-flexbox-spacing-sizes: (
       null    :  $null,
    '--x-small':  $xs,
    '--small'  :  $sm,
    '--medium' :  $md,
    '--large'  :  $lg,
    '--x-large':  $xl,
    '--flush'  :  0,
) !default;


// Specify possible alignment values

$rpb-flexbox-alignment-values: (
    '--top': 'flex-start',
    '--middle': 'center',
    '--bottom': 'flex-end',
    '--baseline': 'baseline',
    '--stretch': 'stretch',
) !default;

// Specify possible justify values
// If you want to omit some of the modifiers,
// or change their suffixes (--middle, --bottom etc.), 
// simply add to the $rpb-flexgrid-justify-values below

$rpb-flexbox-justify-values: (
    '--left': 'flex-start',
    '--center': 'center',
    '--right': 'flex-end',
    '--between': 'space-between',
    '--around': 'space-around',
) !default;



// The Flexbox grid

.rpb-grid {
    display: flex;
    flex-flow: row wrap;
    list-style: none;
    padding: 0;
}

/**
 * 1. Cells are full-width and stack on top of each other by default.
 */

.rpb-grid__cell {
    flex: 0 1 auto;
    width: 100%; /* [1] */
}


// Gutters

/**
 * 1. The map $rpb-flexbox-spacing-sizes contains a `null` key which
 *    is the default gutter size.
 * 2. Add a negative margin on one side only for a wrapper with the 
 *    same amount of spacing.
 * 3. Neutralize the left margin on `rpb-grid`.
 */

@each $size-namespace, $size in $rpb-flexbox-spacing-sizes {
    .rpb-grid#{$size-namespace} { /* [1] */
        margin-left: -$size; /* [2] */
        & > .rpb-grid__cell {
            padding-left: $size; /* [3] */
        }
    }
}

/**
 * Gutter widths provided a modifier class. For example, the following 
 * block will generate a grid with "large" gutters and a grid with no
 * gutters at all:
 *
 * <div class="rpb-grid rpb-grid--large">
 *    <div class="rpb-grid__cell">
 *  </div>
 * </div>
 *
 * <div class="rpb-grid rpb-grid--flush">
 *   <div class="rpb-grid__cell">
 *   </div>
 * </div>
 *
 * Without a modifier, the default size is equal to $rpb-spacing-unit.
 */



// Automatically size cells by distributing them equally
// Stack vertically in mobile

// .rpb-grid--auto {
//     & > .rpb-grid__cell {
//         flex: 1 0 0;
//     }
// }


// Horizontal alignment

/**
 *  Align cells left (default):
 *  .rpb-grid--left {
 *     justify-content: flex-start;
 *  }
 *  
 *  Horizontally center cells:
 *  .rpb-grid--center {
 *    justify-content: center;
 *  }
 *
 *  Align cells right:
 *  .rpb-grid--right {  
 *    justify-content: flex-end;
 *  }
 *
 *  Distribute free space between the cells:
 *  .rpb-grid--between {  
 *    justify-content: space-between;
 *  }
 *
 *  Distribute free space around the cells. 
 *  .rpb-grid--around {
 *    justify-content: space-around;
 *  } 
 *   i.e.:
 *  <div class="rpb-grid--center"></div>
 *
 */

@each $namespace, $value in $rpb-flexbox-justify-values {
    .rpb-grid#{$namespace} {
        justify-content: #{$value};
    }
}


// Pull behavior

/**
 * Pull cells to the left or the right with 
 * the following modifier classes:
 */

.rpb-grid__cell--pull-left {
    margin-right: auto;
}

.rpb-grid__cell--pull-right {
    margin-left: auto;
}


// Vertical alignment

/**
 * .rpb-grid can be used with the following modifiers 
 * for vertical alignment:
 *
 * Stretch cells to fit the container (default): 
 * .rpb-grid--stretch { 
 *   align-items: stretch 
 * }
 *
 * Vertically center cells:
 * .rpb-grid--middle {
 *   align-items: center;
 * }
 *
 * Align cells to the bottom:
 * .rpb-grid--bottom {
 *   align-items: flex-end;
 * }
 *
 * Align cells to the top:
 * .rpb-grid--top {
 *    align-items: flex-start;
 * }
 *
 * Position cells at the baseline of the container:
 * .rpb-grid--baseline {
 *       align-items: baseline;
 * }
 *  i.e.: 
 *
 *  <div class="rpb-grid--middle"></div>
 *
 * You can also align specific cells by using the following modifier classes
 * 
 * .rpb-grid__cell--stretch - Stretch cell to fit the container.
 * .rpb-grid__cell--middle - Vertically align cell.
 * .rpb-grid__cell--bottom - Align cell to the bottom.
 * .rpb-grid__cell--top - Align cell to the top.
 * .rpb-grid__cell--baseline - Position cell at the baseline of the container.
 *
 *
 */

@each $namespace, $value in $rpb-flexbox-alignment-values {
    .rpb-grid#{$namespace} {
        align-items: #{$value};
    }
}

@each $namespace, $value in $rpb-flexbox-alignment-values {
    .rpb-grid__cell#{$namespace} {
        align-self: #{$value};
    }
}


// Content distribution

/**
 * Change the direction cells are placed in the layout with 
 * the following classes:
 */

//Cells are laid out top to bottom instead of left to right:
 .rpb-grid--column {
    flex-direction: column;
}

//Cells are laid out from bottom to top:
.rpb-grid--column-reverse {
    flex-direction: column-reverse;
}

//Place cells from right to left:
.rpb-grid--reverse {
    flex-direction: row-reverse;
}


//Place cells in order
.rpb-order-first {order:1;}
.rpb-order-last  {order:13;}
.rpb-order-0     {order:0;}
.rpb-order-1     {order:1;}
.rpb-order-2     {order:2;}
.rpb-order-3     {order:3;}
.rpb-order-4     {order:4;}
.rpb-order-5     {order:5;}
.rpb-order-6     {order:6;}
.rpb-order-7     {order:7;}
.rpb-order-8     {order:8;}
.rpb-order-9     {order:9;}
.rpb-order-10    {order:10;}
.rpb-order-11    {order:11;}
.rpb-order-12    {order:12;}
.rpb-order-13    {order:13;}
