/* ========================================================================
  #Icons
  ======================================================================== */
 
/**
 * Redefine the sprite elements against tools classes to allow for
 * element manipulation and styling, i.e. colour and scale:
 *
 * .example {
 *   @extend .my--shape;
 * }
 *
 */

//Import rendered sprites, updated when additional elements added to the
//source folder
 @import "core/sprites";


//Fill colour based on icon colour value
[class^="rpb-icon"] > use {
  fill: currentColor;
}

.rpb-icon__search {
  @extend .icons--search;
  color: colour(rpb-slate);
  font-size: 1.4rem;
  min-width: 1.2rem;

  //Windows High contrast
  @media (-ms-high-contrast: active) and (forced-colors: active) {
    filter: invert(100%) sepia(120%) saturate(5000%) hue-rotate(50deg) brightness(815%) contrast(125%);
  }

}



.rpb-icon__arrow-down {
  @extend .icons--arrow-down;
  bottom: 0;
  fill: colour(rpb-slate);
  font-size: 1rem;
  margin: auto;
  position: absolute;
  right: $md;
  top: 0;
  max-width: 100%;
}


//Accessibility homepage
.rpb-icon__wrapper--design {
  padding: $md;
}

.rpb-icon__accessibility {
  @extend .icons--accessibility;
  color: colour(rpb-blue);
  @include font-size(200px);

  @include mq($from: tablet) {
