/* ==========================================================================
   #Page Header
   ========================================================================== */

.rpb-header {
  background: #FFD72F;
  background: linear-gradient(90deg,#0F296A,#ffd72f 82%);
  height: 50px;
  margin: 0;
  padding: $xs 0;
  position: fixed;
  top: 0;
  transition: height 0.3s;
  width: 100%;
  z-index: z(header);

  &.sticky {
    box-shadow: 0 1px 2px 0 rgba(0, 0, 0, 0.5);
    opacity: 0;
    position: fixed;
    top: -50px;
    transition: 0.2s all;
    width: 100%;
    z-index: z(overlay);
  }

  &.show {
    top: 0;
    opacity: 1;
}

}


.rpb-header-wrapper {
  display: flex;
  justify-content: space-between;
}


.rpb-subheader {
  display: flex;  
  @include font-size(16px);
  margin: $null 0 0;
  padding: 0;
  z-index: z(header);
}

.rpb-header__divider {
    border: none;
    padding-bottom: $null;

    @include mq($from: tablet) {
      border-bottom: 1px solid colour(rpb-off-white);
   }
}               

.rpb-header__logotype {
  align-items: flex-start;
  display: flex;
  flex-direction: column;
  margin: 0;
  width: auto;


  @include mq($from: mobile) {
    align-items: center;
    flex-direction: row;
  }

}

.rpb-header__site-logo {
  align-items: center;
  color: colour(rpb-white);
  display: inline-flex;
  font-family: 'Special Elite', serif;
  font-size: $md;
  font-weight: bolder;
  margin: 0;
  text-decoration: none;
}

.rpb-header__site-logo:focus {
  background-color: colour(rpb-focus);
  color: colour(rpb-link-active);
  box-shadow: 0 -2px colour(rpb-focus), 0 4px colour(rpb-link-active);
  outline: 3px solid transparent;
  padding: 0;
  text-decoration: none;

  .rpb-icon__rpb-logo {
    color: colour(rpb-link-active);
  }

}

.rpb-header__product-name {
  color: colour(rpb-white);
  font-size: calc(16px + (22 - 16) * ((100vw - 250px) / (1800 - 250)));
  font-weight: 500;
  line-height: 1.5;
  margin: $sm 0;

  @include mq($from: mobile) {
    margin-left: $md;
  }
  
}

.rpb-header__actions {
  display: flex;
  align-items: center;
  height: 100%;
  justify-content: flex-start;
  margin: $sm 0 0;
  position: relative;
  width: 100%;

  @include mq($from: tablet) {
    justify-content: flex-end;
    margin: 0;
  }
}

