/* ==========================================================================
   #Container
   ========================================================================== */

/**
 * Page-level constraining and container elements.
 */

 $rpb-container-width:   1100px !default;
 $rpb-container-padding: $md !default;

 @if (type-of($rpb-container-width) != number) {
   @error "`#{$rpb-container-width}` needs to be a number.";
 }
 
 .rpb-container{
   margin-right: $md;
   margin-left: $md;
   transition: $rpb-transition;

   @include mq($from: tablet) {
    margin-right: $lg;
    margin-left: $lg;
    max-width: $rpb-container-width;
   }

   @include mq($from: desktop) {
     margin-right: auto;
     margin-left:  auto;
   }

 }
 



