/* ==========================================================================
   #Contact form
   ========================================================================== */



.rpb-contact-form {
    margin: 0 $xs $xxl;
}

.rpb-form-group {
   margin: 0 0 $md; 
   width: 100%;

   @include mq($from: tablet) {
      width: 90%;
    }

}

.rpb-form-control {
   border: 1px solid colour(rpb-blue);
   border-radius: 21px;
   -webkit-appearance: none;
   box-sizing: border-box;
   color: colour(rpb-slate);
   line-height: 1.5;
   margin-bottom: 0;
   padding: $xs $sm;
   width: 100%;
   
   &:focus{
      box-shadow: inset 0 0 0 3px colour(rpb-link-active);
      outline: 3px solid colour(rpb-focus);
      outline-offset: 0;
      border-color: colour(rpb-link-active);
      border-width: 1px;
   }
   }


textarea { 
   border-radius: 16px !important;
   min-height: 142px;
   resize: vertical; 
}