/* ========================================================================
   #Settings
   ======================================================================== */

/**
 * The settings file establishes key setup variables. They set how the SCSS
 * framework functions, only modify and preconfigure with caution. 
 *
 * refer to core/global.scss for site wide variables & shorthand rules
 * 
 */



 
// Baseline grid lines height. Every spacing metric is based on this.
$rpb-baseline: .375rem !default;


// How many grid lines should the spacing unit variants span?
// Each value should be a unitless integer.
$rpb-spacing-unit-factor-xs:      1 !default;   //  extra small .375rem
$rpb-spacing-unit-factor-sm:      2 !default;   //  small .750rem  
$rpb-spacing-unit-factor-sp:      3 !default;   //  small plus 1.125rem 
$rpb-spacing-unit-factor-md:      4 !default;   //  medium 1.5rem
$rpb-spacing-unit-factor-lg:      5 !default;   //  large 1.875rem
$rpb-spacing-unit-factor-xl:      8 !default;   //  extra large 3.0rem
$rpb-spacing-unit-factor-xxl:     16 !default;  //  extra extra large 6.0rem






// Spacing values are determined based on the project’s global baseline grid.
// It is not recommended that you modify the following variables
$rpb-spacing-unit-xs:    $rpb-baseline * $rpb-spacing-unit-factor-xs  !default;
$rpb-spacing-unit-sm:    $rpb-baseline * $rpb-spacing-unit-factor-sm  !default;
$rpb-spacing-unit-sp:    $rpb-baseline * $rpb-spacing-unit-factor-sp  !default;
$rpb-spacing-unit-md:    $rpb-baseline * $rpb-spacing-unit-factor-md  !default;
$rpb-spacing-unit-lg:    $rpb-baseline * $rpb-spacing-unit-factor-lg  !default;
$rpb-spacing-unit-xl:    $rpb-baseline * $rpb-spacing-unit-factor-xl  !default;
$rpb-spacing-unit-xxl:   $rpb-baseline * $rpb-spacing-unit-factor-xxl !default;






// Base typographical styles.
$rpb-font-size:                1rem!default; 
$rpb-line-height:              $rpb-spacing-unit-md !default;
$rpb-quote-width:              .41em !default;
$rpb-open-quote:               “ !default;
$rpb-close-quote:              ” !default;



// Check that the chosen font rules are rem numbers.

@each $_rpb-font-globals in
      $rpb-font-size
      $rpb-line-height {

  @if (type-of($_rpb-font-globals) == number) {

    @if (unit($_rpb-font-globals) != "rem") {
      @error "`#{$_rpb-font-globals}` needs to be a rem value.";
    }

  } @else {
    @error "`#{$_rpb-font-globals}` needs to be a number.";
  }

}


// Check that the chosen size factors are unitless, integer numbers.
@each $_rpb-spacing-unit in
      $rpb-spacing-unit-factor-xs
      $rpb-spacing-unit-factor-xs
      $rpb-spacing-unit-factor-sm
      $rpb-spacing-unit-factor-md
      $rpb-spacing-unit-factor-lg
      $rpb-spacing-unit-factor-xl
      $rpb-spacing-unit-factor-xxl {

  @if (type-of($_rpb-spacing-unit) == number) {

    @if (unitless($_rpb-spacing-unit) == false) {
      @error "`#{$_rpb-spacing-unit}` needs to be unitless.";
    }

    @if ($_rpb-spacing-unit != ceil($_rpb-spacing-unit)) {
      @error "`#{$_rpb-spacing-unit}` needs to be an integer.";
    }

  } @else {
    @error "`#{$_rpb-spacing-unit}` needs to be a number.";
  }

}

