/* ==========================================================================
   #Divider
   ========================================================================== */

/**
 * The divider gives style control to the horizontal rule to provide a visual 
 * break and divide content.
 *
 *  i.e.
 *  <hr class="rpb-divider .rpb-divider--light" aria-hidden="true">
 *
 *  A divider can also be added to the base of the element:
 *
 *  i.e.
 *  <div class="rpb-add-divider rpb-add-divider--light"></div>
 */


//Divider base styles
.rpb-divider {
  @include horizontal-rule(colour(rpb-dark-grey), solid, $xs/3);
  height: 1px;
  margin: $null 0;
}

.rpb-divider--light {
  color: colour(rpb-light-grey);
}

.rpb-divider__title {
  @include horizontal-rule(colour(rpb-slate), solid, $sm/3);
  margin: 0 0 $md;
  width: $xxl;
 }


//Divider applied on existing element
.rpb-add-divider {
  border-bottom-color: colour(rpb-dark-grey);
  border-bottom-width: 1px;
  border-bottom-style: solid; 
  margin-bottom: $lg;
}

.rpb-add-divider--light {
  border-bottom-color: colour(rpb-light-grey);
  border-bottom-width: 1px;
  border-bottom-style: solid; 
  margin-top: $md;
  margin-bottom: $lg;
}
