/* ==========================================================================
   #Page Footer
   ========================================================================== */

.rpb-footer {
    background-color: colour(rpb-slate);
    padding: 0;
    position: relative;
    z-index: z(footer);
}

.rpb-footer__small-text {
    color: colour(rpb-white);
    font-size: $sm;
    margin-bottom: $sp;
    margin-left: 0;
    @include font(primary, regular);

    @include mq($from: tablet) {
      margin-left: $xs;;
    } 

    

     // External icon size
     .rpb-footer__link--external::after {
        left: 0;
        top: 1px;
        width: $sm;
    }
}


//list
.rpb-footer__list {
    display: flex;
    flex-direction: row;
    margin: 0;
    padding: 0;

    @include mq($from: mobile) {
        justify-content: flex-start;
      }

    @include mq($from: tablet) {
      justify-content: flex-end;
      margin: $md 0;
    } 

}

.rpb-footer__list-item {
    
    display: inline-flex;
    margin: 0;
    padding: 0 $sm; 
    transition: $rpb-transition;

    &:first-child {
        margin-left: 0;
        padding-left: 0;
    }

    &:last-child {
        margin-right: 0;
        padding-right: 0;
    }

}

//Connect icons
.rpb-footer__contact {
    justify-content: flex-start;
    margin: $null 0 0;
    padding: $sm; 
}


.rpb-linkedin, .rpb-github, .rpb-twitter, .rpb-phone a {

    &:focus:not(:hover) {
      box-shadow: inset 0 0 0 3px colour(rpb-link-active);
      outline: 3px solid colour(rpb-focus);
      outline-offset: 0;
      border-color: colour(rpb-link-active);
      border-width: 1px;
    }
} 

.rpb-phone {
    display: block;

    @include mq($from: tablet) {
       display: none
    }

}




//links
.rpb-footer__link {
    @include font-size(16px);
    @include font(primary, regular);
    @include linkColours ((colour(rpb-off-white)), (colour(rpb-off-white)), 
    (colour(rpb-white)), (colour(rpb-white)));
    border-bottom-color: transparent;

    &:hover:not(:focus) {
      border-bottom-width: 2px;
    }
}

// External links 
// base64 format used as background image
.rpb-footer__link--external {

  &::after {
    content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 576 512'%3E%3Cpath fill='%23D3D3D3' d='M576 24v127.984c0 21.461-25.96 31.98-40.971 16.971l-35.707-35.709-243.523 243.523c-9.373 9.373-24.568 9.373-33.941 0l-22.627-22.627c-9.373-9.373-9.373-24.569 0-33.941L442.756 76.676l-35.703-35.705C391.982 25.9 402.656 0 424.024 0H552c13.255 0 24 10.745 24 24zM407.029 270.794l-16 16A23.999 23.999 0 00384 303.765V448H64V128h264a24.003 24.003 0 0016.97-7.029l16-16C376.089 89.851 365.381 64 344 64H48C21.49 64 0 85.49 0 112v352c0 26.51 21.49 48 48 48h352c26.51 0 48-21.49 48-48V287.764c0-21.382-25.852-32.09-40.971-16.97z'/%3E%3C/svg%3E");
    display: inline-flex;
    left: 4px;
    position: relative;
    top: 1px;
    width: 1rem;

    //Windows High contrast mode
    @media (-ms-high-contrast: active) and (forced-colors: active) {
      filter: invert(0%) sepia(120%) saturate(5000%) hue-rotate(50deg) brightness(815%) contrast(125%);
    }
      
  }

  &:hover:not(:focus)::after {
    content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 576 512'%3E%3Cpath fill='%23FFF' d='M576 24v127.984c0 21.461-25.96 31.98-40.971 16.971l-35.707-35.709-243.523 243.523c-9.373 9.373-24.568 9.373-33.941 0l-22.627-22.627c-9.373-9.373-9.373-24.569 0-33.941L442.756 76.676l-35.703-35.705C391.982 25.9 402.656 0 424.024 0H552c13.255 0 24 10.745 24 24zM407.029 270.794l-16 16A23.999 23.999 0 00384 303.765V448H64V128h264a24.003 24.003 0 0016.97-7.029l16-16C376.089 89.851 365.381 64 344 64H48C21.49 64 0 85.49 0 112v352c0 26.51 21.49 48 48 48h352c26.51 0 48-21.49 48-48V287.764c0-21.382-25.852-32.09-40.971-16.97z'/%3E%3C/svg%3E");
    }

    &:focus:not(:hover)::after {
        content: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' width='16' height='16' viewBox='0 0 576 512'%3E%3Cpath fill='%230B0C0C' d='M576 24v127.984c0 21.461-25.96 31.98-40.971 16.971l-35.707-35.709-243.523 243.523c-9.373 9.373-24.568 9.373-33.941 0l-22.627-22.627c-9.373-9.373-9.373-24.569 0-33.941L442.756 76.676l-35.703-35.705C391.982 25.9 402.656 0 424.024 0H552c13.255 0 24 10.745 24 24zM407.029 270.794l-16 16A23.999 23.999 0 00384 303.765V448H64V128h264a24.003 24.003 0 0016.97-7.029l16-16C376.089 89.851 365.381 64 344 64H48C21.49 64 0 85.49 0 112v352c0 26.51 21.49 48 48 48h352c26.51 0 48-21.49 48-48V287.764c0-21.382-25.852-32.09-40.971-16.97z'/%3E%3C/svg%3E");
    }

}





//copyright
.rpb-footer__copyright {
    color: colour(rpb-off-white);
    margin-top: $null;
    text-decoration: none;

    &:hover:not(:focus) {
        color: colour(rpb-white);
    }
}

.rpb-footer__copyright--logo {
    color: colour(rpb-off-white);
    margin: $xs $null $md 0;
    align-items: center;
    display: inline-flex;
    text-decoration: none;

    &:focus:not(:hover) {
        background-color: colour(rpb-focus);
        box-shadow: 0 -2px colour(rpb-focus),0 4px colour(rpb-link-active);
        color: colour(rpb-slate);
        outline: 3px solid transparent;
        padding: 0;
        text-decoration: none;
    }

    &:hover:not(:focus) {
        color: colour(rpb-white);
    }

    @include mq($from: mobile) {
        margin: 0 0 $null 0;
    }

}

.rpb-icon__creative-commons {
  margin: .25rem 0;
}

.rpb-footer__copyright--item {
    text-decoration: none;
    display: flex;
    margin-bottom: $xs;
}

