/* ==========================================================================
   #Card
   ========================================================================== */

/**
 * The card object gives control over the vertical alignments of the text and 
 * image within the cards.
 *
 * Update card image  line :112
 *
 */


 // Prevents irregular margin behaviour in Chrome 
.rpb-card--wrapper {
  overflow: hidden;
}
 
.rpb-cards {
  padding: $null 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0;

  @include mq($from: desktop) {
    justify-content: space-between;
  } 
  
}

.rpb-card {
  list-style-type: none;
  margin: 0 0 $null;
  outline: none;
  padding: 0;
}

.rpb-card--link {
  background: none;
  border: 3px solid transparent;
  border-radius: 12px;
  cursor: pointer;
  display: block;
  list-style-type: none;
  margin: $sm 0 $md;
  outline: none;

  &:focus .rpb-card--title span {
    background-color: colour(rpb-focus);
    box-shadow: 0 -2px colour(rpb-focus), 0 4px colour(rpb-link-active);
    color: colour(rpb-link-active);
    text-decoration: none;
  }

}

//card widths
.rpb-card-wrapper {
  background-color: colour(rpb-slate);
  border-radius: 10px;
  box-shadow: rgba(0,0,0,.66) 0 3px 6px 0, inset #2A2A2A 0 0 0 5px,inset rgba(255,255,255,.5) 0 0 0 6px;
  display: flex;
  flex: 0 0 300px;
  height: 380px;
  overflow: hidden;
  position: relative;
  width: 300px;
  transition: .5s cubic-bezier(.445,.05,.55,.95);

  &:hover:not(:focus) .rpb-card--image {
    outline: none;
    opacity: .95;
   
      top: 0px;
      left: 0px;
      width: 100%;
      height: 100%;
   
  }

  @include mq($from: mobile) {
    flex: 0 0 280px;
    height: 360px;
    width: 280px;
  } 


  @include mq($from: tablet) {
    flex: 0 0 260px;
    height: 340px;
    width: 260px;
  }    
  
  @include mq($from: desktop) {
    flex: 0 0 240px;
    height: 320px;
    width: 240px;
  } 

}

.rpb-card--image {
  opacity: .95;
  position: absolute;
  top: -10px;
  left: -10px;
  width: 105%;
  height: 105%;
  padding: 20px;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  transition: .2s cubic-bezier(.445,.05,.55,.95), opacity .2s .4s cubic-bezier(.445,.05,.55,.95);
  pointer-events: none;


  @include mq($from: desktop) {
    opacity: 0.5;
  } 

  //Update images here
  &.one {background-image: url('../images/rpb/export-new-zealand.jpg')};
  &.two {background-image: url('../images/rpb/nz-customs-service.jpg')};
  &.three {background-image: url('../images/rpb/leapfrog-3d.jpg')};
  &.four {background-image: url('../images/rpb/customs-release.jpg')};
  &.five {background-image: url('../images/rpb/bec.jpg')};
  &.six {background-image: url('../images/rpb/iconography-accessibility.jpg')};
  &.seven {background-image: url('../images/rpb/iconography.jpg')};
  &.eight {background-image: url('../images/rpb/kb.jpg')};
  &.nine {background-image: url('../images/rpb/weu.jpg')};
  &.ten {background-image: url('../images/rpb/animation.jpg')};
  &.eleven {background-image: url('../images/rpb/features.jpg')};
  &.twelve {background-image: url('../images/rpb/accessibility.jpg')};
}

.rpb-card--content {
  background-color: rgba(255,255,255,.95);
  bottom: 0;
  color: colour(rpb-white);
  padding: $sm;
  position: absolute;
  transform: translateY(0);
  transition: .25s .5s cubic-bezier(.215,.61,.355,1);
  width: 100%;
}

.rpb-card--title {
    color: colour(rpb-slate);
    @include font-size(18px);
    margin: 0 0 $sm;
    text-decoration: none;
    font-weight: 700;
}

.rpb-card--text {
  color: colour(rpb-slate);
  @include font-size(16px);
  margin: 0 0 $xs;
}

.rpb-card--content * {
  position: relative;
  z-index: z(footer);
}
