/* ========================================================================
  #Colour palette
  ======================================================================== */
 
 /**
 *  Global representation for the given colour, where colour(colour-name) is 
 *  the friendly name for the colour;
 * 
 * .example {
 *   background-color: colour(rpb-brand);
 *   color: colour(white, .95);
 * }
 *
 */



// Main Colour tokens
$colour-map: (

    //Core Rob Bartlett colours
    rpb-black                :           #212121, 
    rpb-dark-grey            :           #363636,  
    rpb-white                :           #FFFFFF,  

    //Grey suite
    rpb-off-white            :           #F4F4F4,  
    rpb-light-grey           :           #D3D3D3, 
    rpb-mid-grey             :           #595959,  
    rpb-slate                :           #2A2A2A,  

    //rpb text colours
    rpb-text                 :           #2A2A2A,  
    rpb-secondary-text       :           #595959,  

    //Alerts
    rpb-error                :           #B10E1E,  
    rpb-warning              :           #D47500,  
    rpb-success              :           #088A20,  
    rpb-info                 :           #1F1BFB,  
    rpb-disabled             :           #AAAAAA,  

    //Interaction states
    rpb-link                 :           #005DBB,  
    rpb-link-visited         :           #4C2C92,  
    rpb-link-hover           :           #000C48,  
    rpb-link-active          :           #0B0C0C,  
    rpb-focus                :           #FFDD00, 

    //Social Media
    linkedIn                 :           #0077B5,  
    facebook                 :           #3b5998,  
    twitter                  :           #55acee,  
    youtube                  :           #FF0000,  

    //Branding Colour
    
    //rpb secondary 
    rpb-blue                 :           #0F296A,
    rpb-dark-blue            :           #10223E,  
    rpb-teal                 :           #2F645B,  
    rpb-green                :           #9C8F47,  
    rpb-red-brick            :           #8F291D,  
    rpb-red                  :           #B53948,
    rpb-indigo               :           #433A6D, 
    rpb-yellow               :           #FFC442,  
    rpb-orange               :           #e09664, 
    rpb-brown                :           #6c4343,    
    
    //Codeview
    code-view                :           #090a0b,
    code-title               :           #f8f8f2,
    code-class               :           #7bcc7b,  
    class-value              :           #7bcc7b, 
    code-highlight           :           #ee9912,
    value-color              :           #dda0dd,
       
) !default;