/* ==========================================================================
   #Buttons
   ========================================================================== */

/**
 * A button is an element that allows users to carry out an action on a page. 
 * Common use cases include allowing a user to Start an application or Save and 
 * continue their progress. A button should have a short text snippet that 
 * describes what it will do.
 * 
 *
 */

 
 /**
  * 1. Allow user to style box model properties.
  * 2. Line different sized buttons up a little nicer.
  * 3. Make buttons inherit font styles (often necessary when styling `input`s as
  *    buttons).
  * 4. Reset/normalize some styles.
  * 5. Force all button-styled elements to appear clickable.
  *
  */
 
 .rpb-btn {
   appearance: none;
   -webkit-appearance: none;
   border: 3px solid transparent;
   display: inline-flex; /* [1] */
   vertical-align: middle; /* [2] */
   font: inherit; /* [3] */
   @include font-size(18px);
   font-weight: 500;
   line-height: 1.5;
   text-align: center; /* [4] */
   margin: 0; /* [4] */
   cursor: pointer; /* [5] */
   padding: $xs $md;
   position: relative;
   transition: $rpb-transition;
   border-radius: 999px;
   text-align: center;

   &:focus:not(:hover) {
    border-color: colour(rpb-focus);
    box-shadow: 0 -2px transparent, 0 4px colour(rpb-link-active);
    background-color: colour(rpb-focus);
    color: colour(rpb-link-active);
    outline: 0;
    text-decoration: none;
   }

   &:focus-visible,
   &:focus-within {
     outline: none;
   }

   &.medium {
     padding: $sm $md;
   }
   &.large {
     padding: $md $lg;
   }
 
 }
 
 
 // Colour Mixin
 @mixin button-bg($bg) {
   background: $bg;
   border-color: $bg;

  &:hover:not(:focus) {
    border-color: darken($bg,8%);
    background:darken($bg,8%);
    transition: all 0.3s ease;
  }
  &:active {
    border-color: darken($bg,25%);
    background:darken($bg,25%);
  } 

}


// Colours
.rpb-btn--white {
  @include button-bg(colour(rpb-white));
  color: colour(rpb-slate);
}

.rpb-button--red {
  @include button-bg(colour(rpb-red));
  color: colour(rpb-white);
}

.rpb-btn--blue {
  @include button-bg(colour(rpb-blue));
  color: colour(rpb-white);
}

.rpb-btn--green {
  @include button-bg(colour(rpb-green));
  color: colour(rpb-white);
}

.rpb-btn--ghost {
  background-color: transparent;
  border-color: (colour(rpb-white));
  color: colour(rpb-white);
}
 