/* ==========================================================================
   #Primary navigation
   ========================================================================== */

 /**
  *  Default navigation, current link referenced via 'aria'current' line :221
  *
  *  An additional link is added to mobile view to avoid Split navigation:
  *  https://www.nngroup.com/articles/split-buttons-navigation/
  * 
  *
  */


// Mobile Navigation Button 
.rpb-navigation__toggle--wrapper {
  display: flex;
  justify-content: space-around;
  margin-left: $sm;
  position: absolute;
  right: $sm;
  top: 0;

  @include mq($from: tablet) {
    display: none;
  }

}

.rpb-navigation__toggle {
  background-color: transparent;
  border: 2px solid colour(rpb-black);
  border-radius: 32px;
  box-shadow: none;
  color: colour(rpb-black);
  cursor: pointer;
  line-height: 1.5;
  margin: 0;
  outline: none;
  overflow: hidden;
  padding: 5px 0;
  position: relative;
  text-decoration: none;
  top: $xs;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  min-width: 75px;
  z-index: z(overlay);

  &:active:focus:not(:hover),
  &:focus:not(:hover),
  &:hover:not(:focus):focus:not(:hover) {
    box-shadow: inset 0 0 0 3px colour(rpb-link-active);
    outline: 3px solid colour(rpb-focus);
    outline-offset: 0;
    border-color: colour(rpb-link-active);
    border-width: 1px;
  }

  &[aria-expanded="true"] {
    border-color: colour(rpb-white);;
    color: colour(rpb-white);

  }



}

// Primary navigation
.rpb-navigation {
  background-color: rgba(42,42,42,.98);
  box-shadow: inset 0 -6px 2px 2px rgb(255, 255, 255), 
              inset 0 1px 2px 0px rgba(0, 0, 0, .75);
  display: none;
  @include font-size(16px);
  height: 100vh;
  left: -1rem;
  padding-top: 3rem;
  position: relative;
  top: -42px;
  width: calc(100% + 2.5rem);
  z-index: 100;

  &.active {
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.4);
    display: block;

    @include mq($from: tablet) {
      box-shadow: none;
    }
  }

  @include mq($from: tablet) {
    background-color: transparent;
    box-shadow: none;
    display: block;
    height: auto;
    left: auto;
    padding-top: 0;
    top: auto;
    width: 100%;
  }


}

.rpb-navigation__list {
  border-bottom: none;
  list-style: none;
  margin: 0;
  padding: 0;
  position: relative;

  @include mq($from: tablet) {
    display: flex;
    flex-direction: row;
    justify-content: flex-end;
    margin-right: 2.25rem;
    margin-left: 2.25rem;
  }

  @include mq($from: desktop) {
    margin-left: auto;
    margin-right: auto;
    max-width: 1100px;
  }

}

.rpb-navigation__list-item {
  border-bottom: 1px solid colour(rpb-mid-grey);
  display: flex;
  margin: 0 $null;
  padding: $null 0;  
  position: relative;

  //hide last bottom-border
  &:last-of-type .rpb-navigation__mobile-toggle:after {
    display: none;
  }


  //expand secondary navigation in mobile
  &.active > .rpb-navigation__level-two {
    display: block;
  
    @include mq($from: tablet) {
      margin-left: 0;
      padding: 0;
    }
  
  }

  @include mq($from: tablet) {
    border-bottom: none;
    display: flex;
    padding: 0;  
    margin: 0 $null;

    //underline
    &:after {
      content: "";
      position: absolute;
      bottom: 3px;
      top: auto;
      left: 0;
      height: 2px;
      background-color: transparent;
      z-index: z(modal);
      width: 100%;
      transition: all 0s ease;
      transition-delay: .25s;
    }

    //underline:hover 
    &.js-focus:after,
    &:not(:focus):hover:after {
      background-color: colour(rpb-slate);
    }

    //current page
    &[aria-current="page"] {
      box-shadow: inset 0 -7px 0 0 colour(rpb-white);
    }


    &:not(:focus):hover > .rpb-navigation-link:after,
    &.js-focus > .rpb-navigation-link:after {
      left: 0;
    }

    &:not(:focus):hover > .rpb-navigation__level-two,
    &.js-focus > .rpb-navigation__level-two {
        left: 0;
        visibility: visible;
    }

    &:last-child:hover .rpb-navigation__level-two,
    &:last-child.js-focus .rpb-navigation__level-two {
        right: 0;
        left: auto;
    }

    &.js-focus .rpb-navigation-sr-expand__wrapper .rpb-navigation-sr-expand__btn:focus::after {
      top: 4px;
       transform: rotate(225deg);
    } 

  }

}

.rpb-navigation__level-one {

  // > .rpb-navigation__list-item:first-of-type {
  //   margin-left: 0;
  // }  
  
  > .rpb-navigation__list-item:last-of-type {
    border-bottom: none;
  }
} 

.rpb-navigation-link {
  border-bottom: 2px solid transparent;
  color: colour(rpb-white);
  display: flex;
  font-size: 1.1rem;
  margin: 0 $md;
  text-decoration: none;

  &:visited {
    color: colour(rpb-white);
  }

  &:hover:not(:focus) {
    border-bottom: 2px solid transparent;
    color: colour(rpb-white);
  }
  
  &:hover,
  &:focus {
    text-decoration: none;
  }

  @include mq($from: tablet) {
    color: colour(rpb-slate);
    display: inline;
    font-size: $null;
    padding: 0;
    position: relative;
    margin: $xs 0 0;
    text-align: center;

    &:active, 
    &:visited,
    &:not(:focus):hover {
      border-bottom: 2px solid transparent;
      color: colour(rpb-slate);
      outline: none;
      text-decoration: none;
    }
  }

}

.rpb-navigation__level-two {
  display: none;
  background-color: colour(rpb-white);
  border-top: 1px solid colour(rpb-light-grey);
  border-bottom: 1px solid colour(rpb-light-grey);
  list-style-type: none;
  margin: 0;
  padding: 0;
  width: 100%;

  @include mq($from: tablet) {
    border-bottom: 5px solid colour(rpb-blue);
    box-shadow: 0 3px 6px 0 rgba(0, 0, 0, 0.5);
    display: block;
    left: -9999em;
    margin: 0;
    padding: $md $xs;
    position: absolute;
    top: 100%;
    transition: all 0s ease;
    transition-delay: 0.25s;
    visibility: hidden;
    width: 280px;
    z-index: z(overlay);
  }

  @include mq($from: desktop) {
    padding: $sm $md;
    width: 320px;
  }

  .rpb-navigation__list-item {
     margin: 2.0rem $null 2.0rem 3.5rem;

    @include mq($from: tablet) {
      margin: 0;
    }
  }

}

//Mobile toggle button
.rpb-navigation__mobile-toggle {
  background: colour(rpb-off-white);
  border: 0;
  border-radius: 0;
  cursor: pointer;
  display: flex;
  margin: 0 $null;
  padding: 1.5rem 0;
  position: relative;
  width: calc(100% - 2rem);

  .rpb-h3 {
    color: colour(rpb-slate);
    font-weight: 500;
    margin-bottom: 0;
    margin-left: $xl;
  }

  &:after {
    background-color: colour(rpb-light-grey);
    content: " ";
    display: block;
    height: 1px;
    left: 0;
    right: 0;
    position: absolute;
    bottom: 0;
  }

  //Hide border when dropdown open
  &[aria-expanded="true"]::after {
    display: none;
  }


  &:before {
    border-top: 3px solid colour(rpb-slate);
    border-right: 3px solid colour(rpb-slate);
    content: "";
    height: $sm;
    left: $sm;
    margin-top: -.025rem;
    pointer-events: none;
    position: absolute;
    top: 35%;
    transform: rotate(135deg) translateY(-50%);
    width: $sm;
  }

   &[aria-expanded="true"]::before {
    transform: rotate(315deg) translate(-35%,75%);
  }

  &:focus-visible {
    outline: 0;
  }

  &:active:focus:not(:hover) .rpb-h3,
  &:active:focus-within:not(:hover) .rpb-h3, 
  &:focus:not(:hover) span,
  &:focus-within:not(:hover) .rpb-h3 {
    background-color: colour(rpb-focus);
    box-shadow: 0 -2px colour(rpb-focus), 0 4px colour(rpb-link-active);
    color: colour(rpb-link-active);
    outline: 3px solid transparent;
    text-decoration: none;
  }

  @include mq($from: tablet) {
    display: none;
  }

}

//Navigation links
.rpb-navigation__list-link {
  border-bottom-color: transparent;
  color: colour(rpb-link);
  display: flex;
  margin: $sm;
  position: relative;
  text-decoration: none;
  width: 100%;

  &:focus-within:not(:hover),
  &:focus {
    outline: 0;
  } 

  // Title within span to avoid full-width underline 
  .rpb-navigation__list-title {
    border-bottom: 2px solid transparent;
    color: colour(rpb-link);
  }


    &:not(:focus):hover {
      border-bottom-color: transparent;
      color: colour(rpb-link-hover);
  
       span {
        border-bottom-color: colour(rpb-link);
         border-bottom-width: 2px;
       }
    }

    &:not(:hover):focus span {
      background-color: colour(rpb-focus);
      box-shadow: 0 -2px colour(rpb-focus), 0 4px colour(rpb-link-active);
      color: colour(rpb-link-active);
      outline: 3px solid transparent;
      text-decoration: none;
    }

}

//Mobile view only links
.rpb-navigation__mobile-link {
  display: block;

  @include mq($from: tablet) {
    display: none;
  }

}

//Desktop dropdown menu keyboard access
.rpb-navigation-sr-expand__wrapper {
  display: none;

  @include mq($from: tablet) {
    border: none;
    display: block;
    margin: 0;
    padding: 0;
    position: relative;
    width: 0;
  }

}
.rpb-navigation-sr-expand__btn {

  @include mq($from: tablet) {
    border: 0;
    clip: rect(0 0 0 0);
    height: 1px;
    margin: -1px;
    overflow: hidden;
    padding: 0;
    position: absolute;
    width: 1px;

    &:focus {
        background: colour(rpb-blue);
        border: none;
        border-radius: 0;
        box-shadow: inset 0 0 0 4px colour(rpb-blue);
        clip: auto;
        height: 14px;
        margin: 0;
        overflow: visible;
        position: absolute;
        outline: 2px solid colour(rpb-focus);
        padding: 0;
        right: -18px;
        top: 40%;
        transition: right .25s ease-in-out;
        width: 14px;

        &::after {
          content: "";
          border-right: 2px solid colour(rpb-white);
          border-bottom: 2px solid colour(rpb-white);
          height: 10px;
          left: 2px;
          position: absolute;
          top: -1px;
          transform: rotate(45deg);
          width: 10px;
        }

    }
  }

  @include mq($from: desktop) {

    &:focus {
      right: -25px;
    }

  }


}







