// /* ==========================================================================
//    #Timeline
//    ========================================================================== */

.rpb-timeline{
  margin-bottom: $xl;
	padding: 0 $xl;
  position: relative;
}

.rpb-timeline-wrapper {
  overflow: hidden;
}

.rpb-timeline-dates {
  box-sizing: border-box;
  list-style: none;
  margin: $xl 0;
  padding: 0 0 $xs;
  position: relative;
  touch-action: pan-y;
  z-index: z(header); 

  &:after {
  	content: "";
  	width: 100%;
	  height: 2px;
	  position: absolute;
    bottom: 12px;
    opacity: .75;
    border-bottom: 2px solid colour(rpb-dark-grey);
  }

}

.rpb-timeline-year {
  font-size: 1.0rem;
  font-weight: normal;
  opacity: 0.4;
  position: relative;
  top: 10px;
  transition: all ease-out .25s;
}

.rpb-timeline-dates__wrapper {
  display: flex;
  height: 100%;
  position: relative;
  -webkit-transform: translate3d(0, 0, 0);
  transform: translate3d(0, 0, 0);
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  -webkit-box-sizing: content-box;
  box-sizing: content-box;
  width: 100%;
  z-index: z(header); 
}

.rpb-timeline-slide {
  -webkit-flex-shrink: 0;
  flex-shrink: 0;
  height: 100%;
  position: relative;
  -webkit-transition-property: -webkit-transform;
  transition-property: -webkit-transform;
  transition-property: transform;
  transition-property: transform, -webkit-transform;
  width: 100%;

  &.active .rpb-timeline-year {
    font-size: 1.25rem;
    opacity: 1;
    top: 0;
  }
}

.rpb-timeline-dates .rpb-timeline-slide {
  color: colour(rpb-black);
  cursor: pointer;
  display: flex;
  font-family: 'Atkinson Hyperlegible', sans-serif;
  font-weight: bold;
  height: 100%;
  justify-content: center;
  min-height: 70px;
  width: auto;
   
  &:after {
    backface-visibility: hidden;
    -webkit-backface-visibility: hidden;
    background-color: colour(rpb-white);
    background-repeat: no-repeat;
    background-position: 50%;
    background-size: 55%;
    border: 2px solid colour(rpb-mid-grey);
    border-radius: 50%;
    bottom: -2.88rem;
    box-shadow: inset 0 0 6px rgba(0,0,0,.25);
    content: "";
    height: 48px;
    position: absolute;
    transform: translateY(-50%);
    transition: all .3s ease-out;
    width: 48px;
  }

  //SVG icons
    &.teaching {

    
      &:after {
        background-image:url(data:image/svg+xml;base64,);
        
          background-size: 60%;
  
         
       }
    }
    &.multimedia:after {
      background-image:url(data:image/svg+xml;base64,);
        background-size: 60%;
    }
    &.animation:after {
      background-image:url(data:image/svg+xml;base64,);
      background-size: 60%;
    }
    &.post-production:after {
      background-image:url(data:image/svg+xml;base64,);
      background-size: 60%;
    }
    &.tertiary:after {
      background-image:url(data:image/svg+xml;base64,);
      background-size: 60%;
    }
    &.html:after {
      background-image:url(data:image/svg+xml;base64,);
      background-size: 60%;
    }
    &.js:after {
      background-image:url(data:image/svg+xml;base64,);
      background-size: 60%;
    }
    &.ux-research {

      &.active:after {
        background-color: colour(rpb-blue);
      }
    
    
      &:after {
        background-image:url(data:image/svg+xml;base64,);
        background-size: 60%;
      }
    }
    &.front-end:after {
      background-image:url(data:image/svg+xml;base64,);
      background-size: 60%;
    }
    &.advisory:after {
      background-image:url(data:image/svg+xml;base64,);
      background-position-y: 40%;
      background-size: 65%;
    }
    &.agile {

      &.active:after {
        background-color: colour(rpb-blue);
      }
      &:after {
        background-image:url(data:image/svg+xml;base64,);
        background-size: 65%;
      }
    }
    &.accessibility:after {
      background-image:url(data:image/svg+xml;base64,);
      background-size: 60%;
    }
    &.ux:after {
      background-image:url(data:image/svg+xml;base64,);
      background-position-y: 40%;
      background-size: 40%;
    }
  

}

.rpb-timeline-dates .active:after {
  background-color: colour(rpb-yellow);
  border-color: colour(rpb-black);
  box-shadow: inset 0 0 2px 1px rgba(0,0,0,.25);
  bottom: -30px;
  height: 64px;
  transform: translateX(-15px);
  width: 64px;
  margin-left: 30px;
}

.rpb-timeline-contents {
    height: 80%;
    width: 100%;
}

.rpb-timeline-slide__content {
  max-width: 40ch;
  margin: 1rem 0 1rem 5%;
}

/* Arrows */
.timeline-buttons-container {
  position: absolute;
  top: 7.45rem;
  right: 0;
  left: 0;
}

.timeline-button-next,
.timeline-button-prev {
    position: absolute;
    top: 50%;
    margin-top: 0;
    z-index: 2;
    cursor: pointer;
    border-radius: 50%;
    width: 28px;
    height: 28px;
    font-size: 13px;
    padding: 5px;
    background-color: colour(rpb-off-white);
    border-radius: 50%;
    border: 2px solid colour(rpb-mid-grey);
    bottom: auto;
    color: transparent;
    height: 34px;
    overflow: hidden;
    padding: 1.25rem;
    position: absolute;
    text-indent: 100%;
    top: 65%;
    transform: translateY(-65%);
    transition: all .3s ease-out;
    white-space: nowrap;
    width: 34px;
    z-index: 6000;

    &:before {
      background: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%232F645B' d='M20 13.1L9.8 23.5c-.7.7-1.7.7-2.4 0s-.7-1.7 0-2.4l9.1-9.1-9.1-9c-.7-.7-.7-1.7 0-2.4s1.7-.7 2.4 0L20 10.8c.3.3.5.8.5 1.2s-.2.8-.5 1.1z'/%3E%3C/svg%3E") no-repeat 0 0;
      bottom: auto;
      content: "";
      position: absolute;
      height: 16px;
      left: 50%;
      right: auto;
      top: 50%;
      transform: translateX(-50%) translateY(-50%);
      width: 16px;
    }

    &:hover {
      background-color: colour(rpb-dark-grey);
      border-color: colour(rpb-dark-grey);
    
      &:before {
        background-image: url("data:image/svg+xml;charset=utf-8,%3Csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 24 24'%3E%3Cpath fill='%23FFFFFF' d='M20 13.1L9.8 23.5c-.7.7-1.7.7-2.4 0s-.7-1.7 0-2.4l9.1-9.1-9.1-9c-.7-.7-.7-1.7 0-2.4s1.7-.7 2.4 0L20 10.8c.3.3.5.8.5 1.2s-.2.8-.5 1.1z'/%3E%3C/svg%3E");
      }
    
     }

    &.rpb-button-disabled {
      opacity: .3;
      cursor: default;
      pointer-events: none;
    }
    
}

.timeline-button-next {
    right: 4px;

    &:before {
      left: 53%;
    }
}

.timeline-button-prev {
    left: 4px;

    &:before {
      left: 30%;
      transform: translateY(-50%) rotate(180deg);
    }
    
}

