/* ==========================================================================
   #Callouts
   ========================================================================== */

/**
  * Default styling for callouts, often positioned to notify the user about 
  * significant information.
  *
  * Reference: https://design.oit.ncsu.edu/docs/nc-state-shortcodes/callout/
  *
  * Border options :65 for _note, _highlight, _important. e.g.
  *
  *   <div class="rpb-callout rpb-callout__note"></div>
  *
  *   Will render as:
  *
  *   .rpb-callout {
  *       border-left: .375rem solid colour(mid-grey);
  *       margin: 1.125rem 0;
  *       padding: 1.125rem;
  *    }
  *
  *   .rpb-callout__note {
  *       background-color: #f4f4f4;
  *       border-left-color: #005987;
  *    }
  *
  */

  .rpb-callout {
    background-color: transparent;
    border-left: .1875rem solid colour(rpb-mid-grey);
    padding: $lg $md;
    margin: $lg 0 $md;

    @include mq($from: tablet) {
      margin-bottom: $lg;
    }

  }

  .rpb-callout__title {
    margin-top: 0;
    margin-bottom: $md;
    margin-left: $lg;
  }

  .rpb-callout__sub-title {
    font-weight: normal;
    margin-left: $lg;
    margin-top: $lg;
  }

  .rpb-callout__text {
    margin-top: $md;
    margin-bottom: $xs;
    margin-left: $sm;


    &:last-child {
      margin-bottom: 0;
    }

  }

  .rpb-callout__link {
    line-height: 1.5;
    margin: $xs $xs $sm $lg;
  }
  
  .rpb-callout__code {
    border-radius: $xs;
  }

  .rpb-callout + .rpb-callout {
    margin-top: -$xs;
  }


// Mixin
@mixin rpb-callout($background, $border-color) {
  background-color: $background;
  border-left-color: $border-color;
}




// Variations
.rpb-callout__note { 
  @include rpb-callout(colour(rpb-off-white), colour(rpb-blue)); 
}

.rpb-callout__highlight { 
  @include rpb-callout(colour(rpb-off-white), colour(rpb-green)); 
}

.rpb-callout__important { 
  @include rpb-callout(colour(rpb-off-white), colour(rpb-red-brick)); 
}