/* ==========================================================================
   #Accordion
   ========================================================================== */

  /**
   * Use accordions to provide a show/hide option for sections of content on 
   * complex pages.
   * 
   *
   * Avoid using an accordion for content that is essential to all users. 
   * Accordions hide content from users and prevent it from being found with 
   * in-page (CTRL-F) search.
   *
   * Reference: https://www.hassellinclusion.com/blog/accessible-accordions-part-2-using-details-summary/
   *
   */

    .rpb-accordion {
      display: flex;
      flex-direction: column;
      margin-top: $lg;
      margin-bottom: $lg;
    }

    .rpb-accordion__controls {
      border-bottom: 1px solid colour(rpb-light-grey);
      display: flex;
      justify-content: flex-end;
   
    .rpb-accordion__toggle {
      background: none;
      border-radius: 0;
      border-width: 0;
      border-bottom: none;
      box-shadow: 0 -2px transparent, 0 2px colour(rpb-link);
      color: colour(rpb-link);
      cursor: pointer;
      @include font-size(18px);
      line-height: 1.65;
      margin-bottom: $null;
      padding: 0;
      z-index: z(footer);

      &:hover:not(:focus) {
        border: none;
        box-shadow: 0 -2px transparent, 0 3px colour(rpb-link-active);
        color: colour(rpb-link-active);
      }

      &:focus-visible:not(:focus) {
        outline: 3px solid transparent;
        text-decoration: none;
      }

    }

  }
    
    .rpb-accordion__details {
      border-bottom: 1px solid colour(rpb-light-grey);
      color: colour(rpb-slate);
      padding: 0;

      & > summary::-webkit-details-marker {
        display: none;
      }

      &[open] > .rpb-accordion__summary span::before {
        height: 0;
      }

    }

    .rpb-accordion__summary {
      cursor: pointer;
      list-style: none;
      margin: $xs 0;
      outline: 0;
      padding: $xs 0;
      position: relative;

      & > * {
        display: inline;
      }

      & .lead {
        display: block;
        margin: $xs $lg 0 0;
        max-width: 60ch;
      }

      &:focus:not(:hover) h2 {
        background-color: colour(rpb-focus);
        border-bottom: none;
        box-shadow: 0 -2px colour(rpb-focus), 0 4px colour(rpb-link-active);
        color: colour(rpb-link-active);
        outline: 3px solid transparent;
        text-decoration: none;
      }

      &:focus-visible {
        outline: 0;
      }

      &:hover:not(:focus) h2 {
        box-shadow: 0 -2px transparent, 0 2px colour(rpb-slate);
        cursor: pointer;
        outline: none;
      }

      &::-webkit-details-marker {
        display: none;
      }

    }

    .rpb-accordion__icon {
      height: 1rem;
      left: 0;
      margin-top: 1px;
      position: absolute;
      top: 15px;
      width: 1rem;
  
        &:before, &:after{
        content: "";
        -webkit-box-sizing: border-box;
        box-sizing: border-box;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        width: 25%;
        height: 25%;
        margin: auto;
        border: 2px solid transparent;
        background-color: colour(rpb-slate);
        }

        &:after {
        width: 100%;
        }    
        &:before {
        height: 100%;
        }    

    }

    .rpb-accordion__title {
      border-bottom-color: transparent;
      font-size: 1.35rem;
      line-height: 1.65;
      margin: 0 $lg;
      padding-bottom: .125vh;
    }

    .rpb-accordion__content {
        margin: $xs 0;
    }











    